export default {
    name: "u-swiper",

    props: {
        list: {
            type: Array,
            default () {
                return [];
            }
        },
        // 是否显示title标题
        title: {
            type: Boolean,
            default: false
        },
        // 用户自定义的指示器的样式
        indicator: {
            type: Object,
            default () {
                return {};
            }
        },
        // 圆角值
        borderRadius: {
            type: [Number, String],
            default: 0
        },
        // 隔多久自动切换
        interval: {
            type: [String, Number],
            default: 3000
        },
        // 指示器的模式，rect|dot|number|round
        mode: {
            type: String,
            default: 'round'
        },
        // list的高度，单位rpx
        height: {
            type: [Number, String],
            default: 250
        },
        // 指示器的位置，topLeft|topCenter|topRight|bottomLeft|bottomCenter|bottomRight
        indicatorPos: {
            type: String,
            default: 'bottomCenter'
        },
        // 是否开启缩放效果
        effect3d: {
            type: Boolean,
            default: false
        },
        // 3D模式的情况下，激活item与前后item之间的距离，单位rpx
        effect3dPreviousMargin: {
            type: [Number, String],
            default: -10
        },
        // 是否自动播放
        autoplay: {
            type: Boolean,
            default: true
        },
        // 自动轮播时间间隔，单位ms
        duration: {
            type: [Number, String],
            default: 500
        },
        // 是否衔接滑动，即到最后一张时接着滑动，是否自动切换到第一张
        circular: {
            type: Boolean,
            default: true
        },
        // 图片的裁剪模式
        imgMode: {
            type: String,
            default: 'aspectFill'
        },
        // 从list数组中读取的图片的属性名
        name: {
            type: String,
            default: 'image'
        },
        // 背景颜色
        bgColor: {
            type: String,
            default: '#f3f4f6'
        }
    },
    watch: {
        // 如果外部的list发生变化，判断长度是否被修改，如果前后长度不一致，重置current值，避免溢出
        list: {
            handler(nVal, oVal) {
                oVal = oVal ? oVal: [];
                if(nVal.length !== oVal.length) this.current = 0;












            },
            immediate: true
        }
    },
    data() {
        return {
            current: 0 // 当前活跃的swiper-item的index
        };
    },





    computed: {
        justifyContent() {
            if (this.indicatorPos == 'topLeft' || this.indicatorPos == 'bottomLeft') return 'flex-start';
            if (this.indicatorPos == 'topCenter' || this.indicatorPos == 'bottomCenter') return 'center';
            if (this.indicatorPos == 'topRight' || this.indicatorPos == 'bottomRight') return 'flex-end';
        },
        titlePaddingBottom() {
            let tmp = 0;
            if (this.mode == 'none') return '12rpx';
            if (['bottomLeft', 'bottomCenter', 'bottomRight'].indexOf(this.indicatorPos) >= 0 && this.mode == 'number') {
                tmp = '60rpx';
            } else if (['bottomLeft', 'bottomCenter', 'bottomRight'].indexOf(this.indicatorPos) >= 0 && this.mode != 'number') {
                tmp = '40rpx';
            } else {
                tmp = '12rpx';
            }
            return tmp;
        },





    },
    methods: {
        listClick(index) {
            this.$emit('click', index);
        },
        change(e) {
            let current = e.detail.current;
            this.current = current;
            // 发出change事件，表示当前自动切换的index，从0开始
            this.$emit('change', current);
        },
        // 头条小程序不支持animationfinish事件，改由change事件
        // 暂不监听此事件，因为不再给swiper绑定current属性
        animationfinish(e) {

            // this.current = e.detail.current;

        },
        getUrlParam(url,name) {
            let search = url.split('?')[1];
            if (search) {
                let r = search.substr(0).match(new RegExp('(^|&)' + name + '=([^&]*)(&|$)'))
                console.log(r);
                if (r !== null) {
                    console.log(unescape(r[2]));
                    return unescape(r[2])
                }
                return null
            } else {
                return null
            }
        }
    },
    filters: {
        // getUrlParam(url, name) {
        //     let search = url.split('?')[1];
        //     if (search) {
        //         let r = search.substr(0).match(new RegExp('(^|&)' + name + '=([^&]*)(&|$)'))
        //         if (r !== null) return unescape(r[2])
        //         return null
        //     } else {
        //         return null
        //     }
        // }
    }
};
